.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter__container {
  display: flex;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 1rem 1rem;
}

@media (max-width: 768px) {
  .expenses-filter__container {
    display: block;
  }

  .expenses-filter__control {
    margin: 1rem 0;
  }
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
  width: 170px;
}
